.container {
  min-width: 180px;
  display: flex;

  align-items: center;
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: #ccc;
  border: 1px solid #aaa;
  width: 30px;
  height: 30px;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 1px 1px 4px #000;
  margin-right: 12px;
}

.name {
  margin-right: auto;
  font-size: 20px;
  text-shadow: 1px 1px 4px #000;
  color: #eee;
}

.button {
  display: block;
  border: none;
  background: linear-gradient(90deg, #55c, #88e, #55c);
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
}
